import Vue from 'vue'  //路由文件必须先引入vue
import Router from 'vue-router'   //引入路由组件
import home from '../components/home'   //引入组件,组件可以作为路由内容,也可以作为自定义html标签
import shop from '../components/shop.vue'
import footer from '../components/footer.vue'
import classification from '../components/classification.vue'
import my from '../components/my.vue'
import login from '../components/login.vue'
import regist from '../components/regist.vue'
import order from '../components/order.vue'
import shopcar from '../components/shopcar.vue'


Vue.use(Router)  //使用路由,use必须得加,表示全局引入

export default new Router({  // 创建路由
    routes: [
        {
            path: '/',
            component:home
        }, 
        {
            path: '/shop/:guid/:title/:time/:freight/static/img/:img',
            component:shop,
            props:true
        },
        {
            path: '/footer',
            component:footer
        }, 
        {
            path: '/classification',
            component:classification
        }, 
        {
            path: '/my',
            component:my
        },
        {
            path: '/login',
            component:login
        }, 
        {
            path: '/regist',
            component:regist
        }, 
        {
            path: '/order',
            component:order
        }, 
        {
            path: '/shopcar',
            component:shopcar
        }, 
    ]
})



